
.map_box {
  background: #fff;
  -webkit-box-flex: 2.7;
      -ms-flex: 2.7;
          flex: 2.7;
  position: relative;
}
.map_box * {
  margin: 0;
  padding: 0;
}
.map_box .map {
  position: absolute;
  width: 100%;
  height: 100%;
}
.map_box .mask {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.5);
}
.map_box .mask .close {
  background: #39c;
  position: absolute;
  right: 0;
  z-index: 22;
}
.map_box .mask .global {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #fff;
  z-index: 9999;
}
.map_box .mask .global .wrap {
  position: absolute;
  width: 100% !important;
  height: 100% !important;
  top: 0;
  left: 0;
}
.map_box .toggle {
  position: absolute;
  top: 1.4rem;
  left: 48%;
  font-size: 0.8rem;
  right: 1rem;
  color: #6ed1e1;
  cursor: pointer;
  z-index: 222;
}
.map_box .toggle strong {
  font-size: 0.5rem;
}
.map_box .map_address {
  position: absolute;
  z-index: 2;
  font-size: 0.4rem;
  top: 0.5rem;
  left: 1.1rem;
  font-style: normal;
  color: #29bbdf;
}
.map_box .map_address .iconfont {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 0.5rem;
}
.map_box .title {
  position: absolute;
  font-size: 0.4rem;
  background: rgba(0,0,0,0.4);
  left: 50%;
  top: 50%;
  z-index: 22;
  padding: 0.1rem;
}
.map_box .title h3 {
  font-size: 0.4rem;
  padding: 0.1rem;
  padding-left: 0;
}
.map_box .title h5 {
  font-size: 0.3rem;
}
.msgs {
  color: #333;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding: 0.1rem 0;
  background: #fefefe;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
  -webkit-box-shadow: 0 0 6px 2px rgba(0,0,0,0.1);
          box-shadow: 0 0 6px 2px rgba(0,0,0,0.1);
}
.msgs h3 {
  color: #f60;
  text-indent: 0.1rem;
  font-size: 0.4rem;
  padding-bottom: 4px;
  border-bottom: 1px solid #fc0;
}
.msgs p {
  color: #39c;
  padding: 1px 0.2rem;
  text-indent: 0.2rem;
  font-size: 0.3rem;
}


