
.education_box {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
}
.education_box .education {
  position: absolute;
  width: 100%;
  height: 100%;
}
.education_box .edtitle {
  position: absolute;
  z-index: 88;
  padding: 0;
  width: 100px;
  background: rgba(0,0,0,0.4);
  font-size: 0.3rem;
}
.education_box .edtitle h4,
.education_box .edtitle small {
  margin: 0;
  padding: 0.05rem;
}


